../www/styles/variables\";\n\n#toolbar {\n  flex-shrink: 0;\n  flex-grow: 0;\n\n  /* iOS 11 && iOS 11.2+ */\n  margin-top: env(safe-area-inset-top);\n  padding-right: env(safe-area-inset-right);\n  padding-left: env(safe-area-inset-left);\n\n  background: var(--paper-color);\n  border-bottom: 1px solid var(--divider-color);\n  min-height: auto;\n\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: center;\n\n  @media screen and (max-width: $smallScreen - 1px) {\n    background-color: var(--background-color) !important;\n    border: none;\n    z-index: 9999;\n    justify-content: flex-end;\n\n    .left {\n      display: none !important;\n    }\n  }\n\n  .left {\n    display: flex;\n    align-items: center;\n    img {\n      height: 22px;\n      margin-top: 5px;\n      margin-left: 10px;\n      color: transparent;\n      &.inverse {\n        -webkit-filter: invert(1);\n        filter: invert(1);\n      }\n    }\n  }\n\n  .right {\n    display: flex;\n    flex-direction: row;\n    justify-content: flex-end;\n    align-items: center;\n\n    hr {\n      border-color: var(--divider-color);\n      width: 1px;\n      height: 30px;\n      margin: 0 4px;\n      padding: 0;\n    }\n  }\n}\n\n@keyframes syncing {\n  0% {\n    transform: scale(1);\n  }\n\n  50% {\n    transform: scale(1.4);\n  }\n\n  100% {\n    transform: scale(1);\n  }\n}\n\n.badgeSync {\n  position: relative;\n\n  &:before {\n    content: \"\";\n    width: 6px;\n    height: 6px;\n    border-radius: 50%;\n    background: white;\n    position: absolute;\n    border: 2px solid var(--primary-color);\n    top: 0;\n    right: -3px;\n    z-index: 999;\n    opacity: 0;\n    transform: scale(0.8);\n    transition: opacity 0.25s, transform 0.25s;\n  }\n  &.open {\n    &:before {\n      opacity: 1;\n      transform: scale(1);\n      transition: opacity 0.25s, transform 0.25s;\n    }\n  }\n  &.isSyncing {\n    &:before {\n      animation: syncing 1s alternate ease-in-out infinite;\n    }\n  }\n}\n\n#container {\n  display: flex;\n  flex-grow: 1;\n  flex-direction: row;\n  overflow: hidden;\n\n  @media screen and (max-width: $smallScreen - 1px) {\n    flex-direction: column-reverse;\n  }\n\n  #fullScreenComponent {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    z-index: 1000;\n    visibility: hidden;\n    transition: visibility 0s 450ms;\n    overflow: hidden;\n\n    .welcoming__wrapper {\n      opacity: 0;\n      visibility: hidden;\n      transform: scale(0.9);\n      transition: visibility 0s 250ms, opacity 250ms, transform 250ms;\n    }\n\n    &.open {\n      visibility: visible;\n      transition: visibility 0s;\n\n      .welcoming__wrapper {\n        opacity: 1;\n        visibility: visible;\n        transform: scale(1);\n        transition: visibility 0s, opacity 250ms, transform 250m;\n      }\n\n      &:before {\n        transform: scale(4);\n        opacity: 1;\n        transition: transform 450ms, opacity 100ms;\n      }\n    }\n\n    &:before {\n      @media screen and (min-width: 601px) {\n        background: rgba(0, 0, 0, 0.2);\n      }\n      @media screen and (max-width: 600px) {\n        background: var(--primary-color);\n      }\n\n      content: \"\";\n      position: absolute;\n      border-radius: 50%;\n      width: 200vw;\n      height: 100vh;\n      bottom: 100vh;\n      left: 0;\n      right: 0;\n      margin-left: -50vw;\n      transform: scale(1);\n      opacity: 0;\n      transition: transform 450ms, opacity 100ms 250ms;\n    }\n  }\n\n  aside.navigation {\n    flex-shrink: 0;\n    flex-grow: 0;\n    z-index: 999;\n\n    @media screen and (min-width: $smallScreen) {\n      width: calc(60px + env(safe-area-inset-left));\n      border-right: solid 1px var(--divider-color);\n      transition: background-color 300ms ease-in-out;\n      background-color: var(--primary-color);\n      /* iOS 11 && iOS 11.2+ */\n      padding-left: constant(safe-area-inset-left);\n      padding-left: env(safe-area-inset-left);\n    }\n\n    footer {\n      display: none;\n    }\n\n    @media screen and (max-width: $smallScreen - 1px) {\n      z-index: 999;\n      width: 100vw;\n      position: relative;\n      background-color: var(--primary-color);\n\n      footer {\n        display: block;\n        border-top: 1px solid var(--divider-color);\n        background-color: var(--paper-color);\n        /* iOS 11 && iOS 11.2+ */\n        padding-bottom: constant(safe-area-inset-bottom);\n        padding-bottom: env(safe-area-inset-bottom);\n      }\n    }\n\n    nav {\n      /* iOS 11 && iOS 11.2+ */\n      padding-top: constant(safe-area-inset-top);\n      padding-top: env(safe-area-inset-top);\n      @media screen and (max-width: $smallScreen - 1px) {\n        display: none;\n      }\n    }\n  }\n\n  #content {\n    overflow: hidden;\n    position: relative;\n    display: flex;\n\n    flex-shrink: 1;\n    flex-grow: 1;\n\n    // toolbar is absolute, so add safe padding top for iPhone x\n    @media screen and (max-width: $smallScreen - 1px) {\n      margin-top: constant(safe-area-inset-top);\n      margin-top: env(safe-area-inset-top);\n    }\n\n    main {\n      width: 100%;\n      height: 100%;\n\n      @media screen and (min-width: $smallScreen) {\n        position: relative;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
