3em;\n  }\n  .dashboard {\n    transition: opacity 0.3s ease-out, transform 0.3s ease-out;\n  }\n  // Used in transactions to have a responsive two column layout\n  .twoColumnContent,\n  .sideListContent {\n    width: 100%;\n    transition: opacity 0.3s ease-out, transform 0.3s ease-out;\n\n    @media screen and (max-width: 919px) {\n      flex-direction: column;\n      .column.hideOnMobile {\n        display: none !important;\n      }\n    }\n    @media screen and (min-width: 920px) {\n      display: flex;\n      height: 100%; // Full height to scroll in column\n      flex-direction: row;\n      overflow: hidden;\n      position: absolute;\n    }\n\n    .column:first-child {\n      display: flex;\n      flex-shrink: 0;\n      align-items: stretch;\n\n      .card {\n        width: 100%;\n        @media screen and (min-width: 920px) {\n          height: 100%;\n        }\n        position: relative;\n        .cardContainer {\n          height: 100%;\n          display: flex;\n          flex-direction: column;\n          @media screen and (min-width: 920px) {\n            overflow: hidden;\n            position: absolute;\n            top: 0;\n            bottom: 0;\n            left: 0;\n            right: 0;\n          }\n          header {\n            position: relative;\n            padding-top: 10px;\n            background: var(--primary-color);\n            h2 {\n              font-weight: 500;\n            }\n            aside {\n              position: absolute;\n              top: 8px;\n              right: 10px;\n            }\n          }\n          article {\n            @include smooth-scroll();\n            font-size: 1.4em;\n            small {\n              text-transform: uppercase;\n              font-size: 0.6em;\n              font-weight: 300;\n            }\n          }\n        }\n      }\n    }\n    .column:last-child {\n      @include smooth-scroll();\n\n      @media screen and (max-width: $smallScreen - 1px) {\n        padding: 18px 15px 5px 15px;\n      }\n      @media screen and (min-width: $smallScreen) and (max-width: 1113px) {\n        padding: 15px 15px 5px 15px;\n        flex: 70%;\n      }\n      @media screen and (min-width: 1114px) {\n        padding: 30px 40px 5px 40px;\n        flex: 70%;\n      }\n\n      @media screen and (min-width: 920px) {\n        .return {\n          display: none;\n        }\n        display: flex;\n        flex-direction: column;\n      }\n\n      .grid {\n        flex: 100%;\n        display: flex;\n        flex-wrap: wrap;\n        .small {\n          @media screen and (max-width: 919px) {\n            width: 100%;\n          }\n          @media screen and (min-width: 920px) {\n            min-width: 330px;\n            max-width: 400px;\n          }\n          padding: 10px;\n        }\n        .large {\n          padding: 10px 30px;\n          @media screen and (max-width: 919px) {\n            width: 100%;\n          }\n          @media screen and (min-width: 920px) {\n            flex-grow: 1;\n            min-width: 310px;\n          }\n        }\n      }\n\n      .fullHeight {\n        @media screen and (max-width: $smallScreen - 1px) {\n          padding: 18px 10px 5px 10px;\n        }\n        @media screen and (min-width: $smallScreen) and (max-width: 1113px) {\n          padding: 10px 10px 10px 10px;\n        }\n        @media screen and (min-width: 1114px) {\n          padding: 25px 10px 25px 10px;\n        }\n        display: flex;\n        flex: 100%;\n        flex-grow: 1;\n        overflow: hidden;\n        .card {\n          flex: 100%;\n          @media screen and (min-width: 920px) {\n            @include smooth-scroll();\n          }\n        }\n      }\n    }\n  }\n  // Dashboard layout\n  .twoColumnContent {\n    .column:first-child {\n      flex-grow: 1;\n      @media screen and (min-width: 640px) {\n        min-width: 360px;\n        flex: 30%;\n      }\n      header {\n        flex-direction: column;\n        align-items: flex-start;\n        h2 {\n          font-size: 2.6em;\n          @media screen and (max-width: $smallScreen - 1px) {\n            padding: 40px 20px 0px 40px;\n            font-size: 2em;\n          }\n          @media screen and (min-width: $smallScreen) and (max-width: 919px) {\n            padding: 40px 20px 0px 40px;\n          }\n          @media screen and (min-width: 920px) {\n            padding: 60px 20px 30px 40px;\n          }\n\n          @media screen and (min-width: 1450px) {\n            font-size: 3.2em;\n          }\n        }\n        .tabs {\n          @media screen and (max-width: 919px) {\n            display: block;\n            width: 100%;\n          }\n          @media screen and (min-width: 920px) {\n            display: none;\n          }\n        }\n      }\n    }\n    .hideOnMobile {\n      @media screen and (max-width: 919px) {\n        display: none;\n      }\n    }\n  }\n  .sideListContent {\n    .column:first-child {\n      flex: 300px;\n      flex-shrink: 0;\n      min-width: 300px;\n      header {\n        h2 {\n          padding: 26px 10px 20px 35px;\n          font-size: 2.8em;\n        }\n      }\n    }\n  }\n  // CSS on change\n  .columnContent {\n    width: 100%;\n    height: 100%; // Full height to scroll in column\n    display: flex;\n    transition: opacity 0.3s ease-out, transform 0.3s ease-out;\n    overflow: hidden;\n\n    .column {\n      flex: 100%;\n      flex-direction: column;\n      @include smooth-scroll();\n\n      @media screen and (max-width: $smallScreen - 1px) {\n        padding: 15px 5px;\n      }\n      @media screen and (min-width: $smallScreen) and (max-width: 1113px) {\n        padding: 15px 15px;\n      }\n      @media screen and (min-width: 1114px) {\n        padding: 30px 40px;\n      }\n      header {\n        padding-top: 10px;\n        margin-bottom: 20px;\n        background: var(--primary-color);\n\n        @media screen and (max-width: $smallScreen - 1px) {\n        }\n        @media screen and (min-width: $sm